* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  opacity: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/***** ABOUT ******/
.canvas {
  top: 0;
  left: 0;
  max-width: 100%;
  height: 100vh;
  z-index: -10;
  position: fixed;
}
.background {
  top: 0;
  left: 0;
  background: linear-gradient(35deg, rgb(17, 42, 97), rgb(79, 9, 97));
  width: 100%;
  height: 100vh;
  z-index: -10;
}
#easle {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: -10;
}

/****** PROJECTS ******/
.card-deck {
  margin: 0;
  padding: 2em 0;
  overflow: hidden;
}

.hovered-img {
  transition: transform .5s ease;
  transform: scale(1.22);
}

/****** CONTACT ******/
.icon-btn {
  width: 100%;
  display: flex;
  justify-content: center;
}
.holder {
  background-color: green;
  color: white;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 2em;
  width: 4em;
  height: 4em;
  overflow: hidden;
  border-radius: 50%;
}
.icon {
  font-size: 4em;
  margin: 3rem 1rem 3rem 0;
  border-radius: 50%;
  background-color: yellow;
}

.holder:hover {
  transition: width 1s, justify-content 2s ease;
  width: 10em;
  border-radius: 10px;
  justify-content: center;
  padding-right: 1rem;
}